import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			user_info: '',
			token:'',
			data:[]
		}
	},
	onLoad(options) {
		this.id = options.id ? options.id : 0;
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			this.initData();
		} else {
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}
	},
	methods: {
		initData(){
			this.request({
				url: interfaces.orderDetail,
				header: {
					token: this.token
				},
				data: {
					id: this.id
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.data = res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		}
	}
}
